var main = document.getElementById('#main');

document.getElementById('land-btn').addEventListener('click', function (evt) {
	var target = evt.target;
	if (target.id === 'land-img') {
		main.getElementById('land-img').style.display = 'visible';
		main.getElementById('animal-img').style.display = 'hidden';
	}
}, false);

document.getElementById('animal-btn').addEventListener('click', function (evt) {
	var target = evt.target;
	if (target.id === 'animal-img') {
		main.getElementById('land-img').style.display = 'hidden';
		main.getElementById('animal-img').style.display = 'visible';
	}
}, false);

export default function update() {


	setTimeout(update, 1000);
}
